function output(props){


    return (
        <div>
            <div id='preview' dangerouslySetInnerHTML={props.setMarkdown}>
            </div>
                <style jsx>{`
                #preview{
                    width: 800px;
                    margin: 50px auto;
                    border: 5px solid black;
                }
                #preview ol,
                #preview ul {
                    text-align: left;
                }
                #preview table {
                    border-collapse: collapse;
                  }
                
                #preview td,
                #preview th {
                    border: 2px solid black;
                    padding-left: 5px;
                    padding-right: 5px;
                  }

                `}</style>
        </div>
    )
}


export default output;